/* menu start */
.ant-menu-horizontal {
  line-height: 37px !important;
  width: 100%;
  display: flex;
  justify-content: space-around;
  border-bottom: none;
}
.ant-menu-horizontal > .ant-menu-item:hover,
.ant-menu-horizontal > .ant-menu-submenu:hover,
.ant-menu-horizontal > .ant-menu-item-active,
.ant-menu-horizontal > .ant-menu-submenu-active,
.ant-menu-horizontal > .ant-menu-item-open,
.ant-menu-horizontal > .ant-menu-submenu-open,
.ant-menu-horizontal > .ant-menu-item-selected,
.ant-menu-horizontal > .ant-menu-submenu-selected {
  font-weight: bold;
  border-image: -webkit-linear-gradient(0deg, $primary-color, $secondary-color) 3;
  border-width: 0px 0px 3px 0px;
}
/* menu end  */

/* form start */
// 所有欄位顏色全黑
.ant-input,
.ant-select-selection-item {
  color: $color-black;
}

// label
.ant-form-item-label {
  padding-bottom: 0 !important;

  // 題目太長的話
  &.label-inline label {
    display: inline;
  }
}

.ant-form label {
  font-size: 16px;
  font-weight: 600;
}

//radio
.ant-radio-inner {
  background-color: white;
  border-color: $secondary-color;
}

// checkbox
.ant-checkbox-inner {
  border-color: $primary-color;
}

.ant-checkbox-wrapper {
  @media screen and (max-width: 767px) {
    width: 100%;
  }
}
//error msg
.ant-form-item-has-error .ant-form-item-explain {
  color: $primary-color;
  font-weight: 300;
}
.ant-form-item-explain,
.ant-form-item-extra {
  color: $primary-color;
  font-weight: 300;
}
// disabled
.ant-input[disabled],
.ant-select-disabled.ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
  background-color: $color-minor-gray;
  color: $color-black;
  border: none;
  height: 40px;
}
.ant-input-affix-wrapper-disabled {
  background-color: $color-minor-gray;
  color: $color-normal-gray;
  border: none;
  height: 40px;
  align-items: center;
}

// fix ipad safari bug
.ant-select-selection-search-input {
  padding: 0;
}

// 在vertical form使部分欄位排row
.ant-form-item.layout-row {
  flex-direction: row;
  align-items: center;
  margin: 0;
  padding: 12px 0; // 調整原本mb

  .ant-form-item-label {
    margin-right: 16px;
  }
}

/* form end */

/* table start */
// 紅色 table
.ant-table-wrapper[headColor="main-red"] {
  .ant-table {
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.15);
    border-radius: 6px;
  }

  .ant-table-thead > tr > th {
    padding: 10px 16px;
    background-color: $primary-color;
    color: white;
  }
  //無padding
  &[paddingStyle="none"] {
    .ant-table-thead > tr > th {
      padding: 0px;
    }
    .ant-table-tbody > tr > td {
      padding: 16px 0px;
    }
  }

  .ant-table-tbody > tr.ant-table-row:hover > td {
    background-color: transparent;
  }

  .ant-table-tbody > tr > td {
    border-bottom: 0;
  }

  .ant-table-container table > thead > tr:first-child th:first-child {
    border-top-left-radius: 6px;
  }

  .ant-table-container table > thead > tr:first-child th:last-child {
    border-top-right-radius: 6px;
  }

  // table 分頁
  nz-pagination {
    justify-content: center;
  }

  .ant-pagination-item:hover {
    border-color: $secondary-color;
    a {
      color: $secondary-color;
    }
  }

  .ant-pagination-item-active {
    border-color: $secondary-color;
    a {
      color: $secondary-color;
    }
  }

  .ant-pagination-prev:focus-visible .ant-pagination-item-link,
  .ant-pagination-next:focus-visible .ant-pagination-item-link,
  .ant-pagination-prev:hover .ant-pagination-item-link,
  .ant-pagination-next:hover .ant-pagination-item-link {
    border-color: $secondary-color;
    color: $secondary-color;
  }
}
//table的排序箭頭顏色
.ant-table-column-sorter-inner {
  color: $color-white;
}
.ant-table-column-sorter-up.active,
.ant-table-column-sorter-down.active {
  display: none;
}
/* table end */

/* tabs start */

.ant-tabs .ant-tabs-top-content,
.ant-tabs .ant-tabs-bottom-content {
  border: 1px solid $primary-color;
  border-radius: 7px;
  border-top-left-radius: 0;
  padding: 20px;

  @media screen and (max-width: 767px) {
    padding: 10px;
  }
}

.ant-tabs.ant-tabs-card .ant-tabs-card-bar .ant-tabs-tab {
  background-color: $color-white;
  border: 1px solid $secondary-color;
  border-bottom: 0px;
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
  padding: 4px 26px;
}

.ant-tabs.ant-tabs-card .ant-tabs-card-bar .ant-tabs-tab-active {
  background-color: $secondary-color;
  color: white;
  border: 1px solid $secondary-color;
  border-bottom: 0px;
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
}

.ant-tabs.ant-tabs-card .ant-tabs-extra-content {
  line-height: 30px;
}
.ant-tabs-tab-btn:focus,
.ant-tabs-tab-remove:focus,
.ant-tabs-tab-btn:active,
.ant-tabs-tab-remove:active {
  color: unset;
}
merc-tab.merc-tab-secondary > .ant-tabs.ant-tabs-card > .ant-tabs-nav .ant-tabs-tab {
  background-color: white;
}
/* 調整Tab end */
/* tabs end */

/* upload start */
.ant-upload-list {
  display: none;
}

.ant-upload.ant-upload-select {
  width: 100%;
}
/* upload end */

/* 其他保單、的error tip 會跑版所以要用絕對定位 start */
.other-insurance-group {
  @media screen and (min-width: 1200px) {
    .ant-form-item-has-error .ant-form-item-explain {
      position: absolute;
      bottom: -25px;
    }
  }
}

.know-mli-from,
.invest-no {
  .ant-form-item-has-error .ant-form-item-explain {
    position: absolute;
    bottom: -25px;
  }
}
/* 其他保單的error tip 會跑版所以要用絕對定位 end */

/* disabled radio & checkbox 顏色改黑色方便閱讀 start */
.ant-radio-checked + span,
.ant-checkbox-checked + span {
  color: $color-black;
  font-weight: 500;
}

.ant-radio-disabled .ant-radio-inner::after {
  background-color: $color-black;
}

.ant-checkbox-disabled.ant-checkbox-checked .ant-checkbox-inner::after {
  border-color: $color-black;
}
/* disabled radio & checkbox 顏色改黑色方便閱讀 end */

/* button start */
.ant-btn {
  font-size: map-get($map: $font-sz, $key: "md");
}
// 專案列表hover顯示查看詳情
.projects .ant-btn {
  background-color: transparent;
  color: $color-white;
  font-size: 20px;

  &:hover,
  :active,
  :focus {
    border-color: $color-white;
  }
}
/* button end */
/* modal */
.white-close-x {
  .ant-modal-close-x {
    color: white;
  }
}
/* modal*/

hr {
  width: 98%;
  margin: 0 auto;
  opacity: 0.25;
}
.ant-dropdown {
  background: $color-white;
  box-shadow: 2px 2px 2px 1px rgb(0 0 0 / 20%);
  border-radius: 3px;
}
