@import "custom-setting.scss";

.toggle-float-btn {
    display: flex;
    position: absolute;
    top: 0;
    right: 0;
    background-color: map-get($base-tab, tab-color);
    color: white;
    border: 1px solid map-get($base-tab, tab-color);
    border-bottom: 0px;
    font-size: 18px;
    font-weight: 600;
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
    height: 40px;
    padding: 0 16px;
    line-height: 38px;
    cursor: pointer;

    .toggle-wrap {
        line-height: 30px;
        display: flex;
        align-items: center;
    }
}

merc-tab {
    /* 顏色 start */
    // 主題色
    &[mercType="primary"] {
        > .toggle-float-btn {
            background-color: $primary-color;
            border: 1px solid $primary-color;
        }
    }

    // 主題色變形
    &[mercType="primaryVariant"] {
        > .toggle-float-btn {
            background-color: $primary-variant-color;
            border: 1px solid $primary-variant-color;
        }
    }

    // 次要色
    &[mercType="secondary"] {
        // > .ant-tabs.ant-tabs-card {
        //     > .ant-tabs-content-holder > .ant-tabs-content > .ant-tabs-tabpane {
        //         merc-toggle-collapse .toggle-float-btn {
        //             background-color: $secondary-color;
        //             border: 1px solid $secondary-color;
        //         }
        //     }
        // }
        .toggle-float-btn {
            background-color: $secondary-color;
            border: 1px solid $secondary-color;
        }
    }

    // 次要色變形
    &[mercType="secondaryVariant"] {
        > .toggle-float-btn {
            background-color: $secondary-variant-color;
            border: 1px solid $secondary-variant-color;
        }
    }
    // 第三色
    &[mercType="third"] {
        // > .ant-tabs.ant-tabs-card {
        //     > .ant-tabs-content-holder > .ant-tabs-content > .ant-tabs-tabpane {
        //         .toggle-float-btn {
        //             background-color: $third-color;
        //             border: 1px solid $third-color;
        //         }
        //     }
        // }
        .toggle-float-btn {
            background-color: $third-color;
            border: 1px solid $third-color;
        }
    }
    // 第三色變形
    &[mercType="thirdVariant"] {
        > .toggle-float-btn {
            background-color: $third-variant-color;
            border: 1px solid $third-variant-color;
        }
    }
    /* 顏色 end */
    /* tab 高度 start */
    // 小
    &[mercSize="small"] {
        > .toggle-float-btn {
            height: 30px;
        }
    }
    // 中
    &[mercSize="normal"] {
        > .toggle-float-btn {
            height: 42px;
        }
    }
    // 大
    &[mercSize="large"] {
        > .toggle-float-btn {
            height: 48px;
        }
    }
    /* tab 高度 end */
}
