/** 基礎功能色設定 */
$color-white: #fff;
$color-dark: rgba(0, 0, 0, 0.8);
// 主要色
$primary-color: #e6002d;
// 主要色變形
$primary-variant-color: #ec6122;
// 次要色
$secondary-color: #ff6621;
// 次要色變形
$secondary-variant-color: #f8c73f;
// 第三色
$third-color: #58a8dd;
// 第三色變形
$third-variant-color: #fffaf1;

// 淺灰
$color-light-gray: #efefef;
// 深灰
$color-dark-gray: #333333;
// 白
$color-white: #fff;
$color-silver: rgba(153, 153, 153, 0.2);
$color-lighter-gray: #f9f9f9;
$color-green: #87b848;

/** 基礎元件設定 */
$font-sz: (
  xl: 25px,
  lg: 18px,
  md: 16px,
  sm: 14px,
  xs: 12px,
);

$font-weight: (
  fatty: 600,
  normal: normal,
);

/** 常用元件設定 */
/* base btn start */
$btn: (
  border-radius: 3px,
  shadow: 1px 1px 2px 0 rgba(21, 28, 42, 0.17),
);
$btn-height: (
  lg: 40px,
  md: 35px,
  sm: 30px,
);
$btn-width: (
  xl: 200px,
  lg: 160px,
  md: 120px,
  sm: 90px,
  xs: 70px,
);
/* base btn end */

/** base card start*/
$base-card: (
  background-color: $color-white,
  box-shadow: none,
  padding: 0,
  border: $color-silver 1px solid,
  border-radius: 6px,
);

$base-card-header: (
  align-self: center,
  font-size: map-get($font-sz, xl),
  font-weight: map-get($font-weight, fatty),
  padding: 10px 20px,
);

$base-card-content: (
  padding: 35px 15px,
);
/** base card end*/

/* base table start */
$base-table: (
  // hover 顏色
  hover-background-color: darken($color-light-gray, 15%),
  // 間隔色
  even-col-color: $third-variant-color,
  // 圓角
  radius: 7px,
  // 陰影
  shadow: 1px 2px 4px #d5d5d5,
  // 表頭、內容（th、td） 間隔
  padding: 8px 8px,
  // 手機版表頭、內容（th、td） 間隔（flex table 專用）
  mo-padding: 3px 8px,
  // 幾px以下使用手機版（flex table 專用）
  break-point: 767px,
  // 欄位下方的border-bottom（flex table 專用）
  mo-border-bottom-width: 0px
);

/* base tab start */
$base-tab: (
  // 顏色(merc-tab 未傳入 mercType 時有效)
  tab-color: $primary-color,
  // tab 邊框
  tab-border-width: 1px,
  // content 邊框
  content-border-width: 1px,
  // 圓角
  radius: 7px,
  // 字體大小
  font-size: map-get($font-sz, lg)
);
/* base tab end */
@import "../../../node_modules/merc-lib/src/styles/merc-lib.scss";
