@import "custom-setting.scss";

/* overwrite table style */
merc-table {
    .ant-table-container {
        overflow-x: auto;
    }

    .ant-table-header,
    .ant-table-body {
        overflow-y: overlay !important;
    }

    .ant-table-thead > .ant-table-row,
    thead > tr {
        white-space: nowrap !important;
    }

    .ant-table-thead > tr > th {
        .ant-table-column-has-sorters {
            padding: 0px;
        }
    }

    // 圓角
    .ant-table-container table > thead {
        tr:first-child th:first-child {
            border-top-left-radius: map-get($base-table, radius);
        }
        tr:first-child th:last-child {
            border-top-right-radius: map-get($base-table, radius);
        }
    }

    .ant-table-tbody > tr > td {
        padding: map-get($base-table, td-padding);
    }

    .ant-table-placeholder .ant-table-expanded-row-fixed {
        margin: -4px -8px;
        min-width: 100%;
    }

    // 間隔色
    .ant-table-tbody {
        tr:nth-child(even) {
            background-color: map-get($base-table, even-col-color);
        }
    }

    // hover 編輯&&刪除
    .hover-to-edit {
        display: block;
        opacity: 0;
        background-color: transparent;
        position: absolute;
        top: 50%;
        right: 26px;
        transform: translateY(-50%);
        display: flex;
        flex-wrap: nowrap;
        transition: opacity 0.3s, background-color 0.3s;
    }

    .ant-table-tbody > tr.ant-table-row:hover > td,
    .ant-table-tbody > tr:hover:not(.ant-table-expanded-row):not(.ant-table-row-selected) {
        background-color: map-get($base-table, hover-background-color);
        .hover-to-edit {
            opacity: 1;
        }
    }

    /* header 顏色 start */
    // 主題色
    &.merc-table-primary {
        .ant-table-thead > tr > th {
            background-color: $primary-color;
        }
    }
    // 主題色變形
    &.merc-table-primary-variant {
        .ant-table-thead > tr > th {
            background-color: $primary-variant-color;
        }
    }
    // 次要色
    &.merc-table-secondary {
        .ant-table-thead > tr > th {
            background-color: $secondary-color;
        }
    }
    // 次要色變形
    &.merc-table-secondary-variant {
        .ant-table-thead > tr > th {
            background-color: $secondary-variant-color;
        }
    }
    // 第三色
    &.merc-table-third {
        .ant-table-thead > tr > th {
            background-color: $third-color;
        }
    }
    // 第三色
    &.merc-table-third-variant {
        .ant-table-thead > tr > th {
            background-color: $third-variant-color;
        }
    }
    /* header 顏色 end */
}

// 緊鄰在card下方的不要圓角
merc-card-content > merc-table {
    .ant-table-container table > thead {
        tr:first-child th:first-child {
            border-top-left-radius: 0;
        }
        tr:first-child th:last-child {
            border-top-right-radius: 0;
        }
    }
}
/* end of overwrite table style */
