@import "custom-setting.scss";

.merc-btn {
    display: inline-block;

    button {
        box-shadow: map-get($btn, shadow);
        border-radius: map-get($btn, border-radius);
        display: flex;
        align-items: center;
        justify-content: center;
    }

    /* 顏色 start */
    // 主題色
    &.merc-btn-primary {
        button {
            color: $color-white;
            background-color: $primary-color;
            border: 1px solid $primary-color;

            &:active,
            &:hover {
                background-color: darken($primary-color, 8%);
                border: 1px solid darken($primary-color, 8%);
            }
        }

        &.merc-btn-outline button {
            border: 1px solid $primary-color;
            background-color: $color-white;
            color: $primary-color;
            &:active,
            &:hover {
                color: darken($primary-color, 15%);
                border: 1px solid darken($primary-color, 15%);
            }
        }
    }

    // 主題色變形
    &.merc-btn-primary-variant {
        button {
            color: $color-white;
            background-color: $primary-variant-color;
            border: 1px solid $primary-variant-color;
            &:active,
            &:hover {
                background-color: darken($primary-variant-color, 8%);
                border: 1px solid darken($primary-variant-color, 8%);
            }
        }
        &.merc-btn-outline button {
            border: 1px solid $primary-variant-color;
            background-color: $color-white;
            color: $primary-variant-color;
            &:active,
            &:hover {
                color: darken($primary-variant-color, 15%);
                border: 1px solid darken($primary-variant-color, 15%);
            }
        }
    }
    // 次要色
    &.merc-btn-secondary {
        button {
            color: $color-white;
            background-color: $secondary-color;
            border: 1px solid $secondary-color;
            &:active,
            &:hover {
                background-color: darken($secondary-color, 8%);
                border: 1px solid darken($secondary-color, 8%);
            }
        }
        &.merc-btn-outline button {
            border: 1px solid $secondary-color;
            background-color: $color-white;
            color: $secondary-color;
            &:active,
            &:hover {
                color: darken($secondary-color, 15%);
                border: 1px solid darken($secondary-color, 15%);
            }
        }
    }
    // 次要色變形
    &.merc-btn-secondary-variant {
        button {
            color: $color-white;
            background-color: $secondary-variant-color;
            border: 1px solid $secondary-variant-color;
            &:active,
            &:hover {
                background-color: darken($secondary-variant-color, 8%);
                border: 1px solid darken($secondary-variant-color, 8%);
            }
        }
        &.merc-btn-outline button {
            border: 1px solid $secondary-variant-color;
            background-color: $color-white;
            color: $secondary-variant-color;
            &:active,
            &:hover {
                color: darken($secondary-variant-color, 15%);
                border: 1px solid darken($secondary-variant-color, 15%);
            }
        }
    }
    // 第三色
    &.merc-btn-third {
        button {
            color: $color-white;
            background-color: $third-color;
            border: 1px solid $third-color;
            &:active,
            &:hover {
                background-color: darken($third-color, 8%);
                border: 1px solid darken($third-color, 8%);
            }
        }
        &.merc-btn-outline button {
            border: 1px solid $third-color;
            background-color: $color-white;
            color: $third-color;
            &:active,
            &:hover {
                color: darken($third-color, 15%);
                border: 1px solid darken($third-color, 15%);
            }
        }
    }
    // 第三色變形
    &.merc-btn-third-variant {
        button {
            color: $color-white;
            background-color: $third-variant-color;
            border: 1px solid $third-variant-color;
            &:active,
            &:hover {
                background-color: darken($third-variant-color, 8%);
                border: 1px solid darken($third-variant-color, 8%);
            }
        }
        &.merc-btn-outline button {
            border: 1px solid $third-variant-color;
            background-color: $color-white;
            color: $third-variant-color;
            &:active,
            &:hover {
                color: darken($third-variant-color, 15%);
                border: 1px solid darken($third-variant-color, 15%);
            }
        }
    }

    &.merc-btn-outline button {
        &:active,
        &:hover {
            > * {
                transition: filter 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
                filter: brightness(0.6);
            }
        }
    }
    /* 顏色 end */

    /* 高度 start */
    // 小
    &.merc-btn-small button {
        height: map-get($btn-height, sm);
    }
    // 中
    &.merc-btn-normal button {
        height: map-get($btn-height, md);
    }
    // 大
    &.merc-btn-large button {
        height: map-get($btn-height, lg);
    }
    /* 高度 end */

    /* 寬度 start */
    // xs
    &.merc-btn-xs button {
        width: map-get($btn-width, xs);
    }
    // sm
    &.merc-btn-sm button {
        width: map-get($btn-width, sm);
    }
    // md
    &.merc-btn-md button {
        width: map-get($btn-width, md);
    }
    // lg
    &.merc-btn-lg button {
        width: map-get($btn-width, lg);
    }
    // xl
    &.merc-btn-xl button {
        width: map-get($btn-width, xl);
    }
    /* 寬度 end */

    /* Disabled */
    &.disabled button[disabled] {
        color: rgba(0, 0, 0, 0.25);
        background: #f5f5f5;
        border-color: #d9d9d9;
        text-shadow: none;
        box-shadow: none;
    }
}
