/* You can add global styles to this file, and also import other style files */
// overwrite breakpoints must write before import bootstrap.

@import "custom-setting.scss";
@import "../src/app/shared/styles/global-overwrite-antdesign.scss";
$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 1024px,
  xl: 1200px,
);

@import "../../../node_modules/bootstrap/scss/bootstrap-grid.scss";
@import "../../../node_modules/bootstrap/scss/bootstrap-utilities.scss";
$colors: (
  "white": $color-white,
  "dark": $color-dark,
  "gray": $color-dark-gray,
  "primary": $primary-color,
  "secondary": $secondary-color,
  "third": $third-color,
);

/* font-type start */
// font-color
@each $type, $color in $colors {
  .text-#{$type} {
    color: $color !important;
  }
}

// font-size
@each $type, $size in $font-sz {
  .text-#{$type} {
    font-size: $size;
  }
}
.text-20 {
  font-size: 20px;
}

// font-weight
.text-fatty {
  font-weight: map-get($map: $font-weight, $key: fatty);
}
/* font-type end */

.btn-self-position {
  margin-left: calc(50% - 80px);
}
/* 大標題 start */
.section-title {
  font-size: 36px;
  font-weight: 600;
  color: $color-black;
  padding-bottom: 20px;
  @media screen and(max-width:575px) {
    font-size: 22px;
  }
}
/* 大標題 end */
/* overwrite full calendar*/
.fc .fc-daygrid-day-number {
  color: $color-dark;
}
.fc .fc-col-header-cell-cushion {
  color: $color-dark;
}
@media screen and (max-width: 414px) {
  .merc-calendar .rocYear-title {
    font-size: 26px;
  }
}
/* overwrite full calendar*/

/* Tab start */
// tab內容用box 包裹
.tab-box {
  padding: 24px 16px;
}

// 修正...tab高度
.ant-tabs > .ant-tabs-nav .ant-tabs-nav-more,
.ant-tabs > div > .ant-tabs-nav .ant-tabs-nav-more {
  padding: 0px 16px;
}

.ant-tabs-content > .ant-tabs-tabpane {
  overflow: hidden;
}
/* Tab end */

/* Table start */

// table header 加前方直槓槓
.table-header {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  font-weight: 700;
  font-size: 20px;
  padding: 1rem;
  @media screen and(max-width:575px) {
    padding: 1rem 0 0 0;
  }
  &::before {
    content: "";
    display: inline-block;
    width: 5px;
    height: 25px;
    vertical-align: middle;
    background-color: $primary-color;
    margin-right: 16px;
  }
}

// table-header 搜尋結果調整
.table-header .page-range {
  font-size: map-get($map: $font-sz, $key: md);
  font-weight: normal;
  margin-left: auto;
  > span {
    margin: 10px;
  }
}

// table pagination start
.table-header {
  .pagination {
    font-size: map-get($map: $font-sz, $key: md);
    font-weight: normal;
    margin-left: auto;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: flex-end;
  }
  .pagesize {
    display: flex;
    align-items: center;
    nz-select {
      padding: 0 8px;
    }
  }
  .pagechange {
    merc-icon {
      cursor: pointer;
      padding: 0 4px;
      img {
        vertical-align: sub;
      }
    }
  }
}

@media screen and (max-width: 575px) {
  .pagesize {
    padding-top: 8px;
    padding-bottom: 8px;
  }
}
// table pagination end

@media only screen and (max-width: map-get($base-table, break-point)) {
  // overwrite merc-lib flex-table
  .merc-flex-table tbody tr {
    &:first-child {
      border-top: 1px solid #d5d5d5 !important;
    }
    &:nth-child(even) {
      background-color: transparent;
    }
    margin-bottom: 12px;
    border: 1px solid #d5d5d5;
    box-shadow: 1px 1px 2px #d5d5d5;
    padding: 10px 0;

    // 避免手機版無值時會被擠上去
    td {
      min-height: 28px;
    }
  }

  // 手機版固定高，內容長捲軸
  .table-content {
    max-height: 400px;
    overflow: auto;
    padding: 0 12px;
  }
}

/* Table end */

/* btn-groups start*/
// 按鈕群組，按鈕畫面置中
.btn-groups {
  display: flex;
  justify-content: center;
  merc-button:first-child {
    margin-right: 24px;
  }
}

.ant-modal .btn-groups {
  margin-top: 10px;
  margin-bottom: 10px;
}

@media screen and (max-width: map-get($map: $grid-breakpoints, $key: sm)) {
  .btn-groups {
    flex-wrap: wrap;
    justify-content: space-evenly;
    merc-button:first-child {
      margin-right: 0px;
      margin-bottom: 16px;
    }
  }
}

// 靠右對齊
.search-btns {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  merc-button:first-child {
    margin-right: 10px;
  }
}
/* btn-groups end*/

/* 搜尋日曆 ~ start */
.date-picker-wave::after {
  content: "~";
  position: absolute;
  right: -17px;
  line-height: 40px;
}
/* 搜尋日曆 ~ end */

// 標題加上前綴icon
.subject {
  font-size: 20px;
  font-weight: map-get($map: $font-weight, $key: "fatty");
  position: relative;
  .icon-prefix {
    position: absolute;
    left: -26px;
    top: -2px;
  }
}
/* 標題區（左邊有紅線） start */
.list-title-zone {
  display: flex;
  margin-bottom: 15px;

  .list-title {
    &::before {
      content: "";
      display: inline-block;
      width: 5px;
      height: 25px;
      background-color: $primary-color;
      vertical-align: middle;
      margin-right: 20px;
      margin-bottom: 2px;
    }
  }

  .list-count {
    margin-left: auto;

    .list-count-number {
      margin: 0 12px;
    }
  }
}
/* 標題區（左邊有紅線） end */
.label-required {
  &::after {
    content: "*";
    color: $primary-color;
  }
}

/* 上傳檔案 btn */
.upload {
  border: 1px solid $secondary-color;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
}

.ant-input-search .ant-input:hover,
.ant-input-search .ant-input:focus {
  &.upload {
    border-color: $secondary-color;
  }
}

.upload-btn .ant-btn {
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
}
/* custom column start */
.custom-row {
  .info-box {
    padding: 15px;

    @media screen and (max-width: 767px) {
      padding: 20px 10px;
    }
    .info-box-content {
      padding: 20px 48px 10px 48px;
      @media screen and (max-width: 767px) {
        padding: 12px;
      }
    }
  }
  .info-box:nth-child(even) {
    background-color: $color-lighter-gray;
  }
}
/* custom column end */

.ant-modal hr {
  margin: 10px auto;
}

.sign-zone {
  max-width: 100% !important;
}
