@import "custom-setting.scss";
.merc-calendar{
    .rocYear {
        display: flex;
        width: 100%;
        align-items: center;
        @media screen and (max-width: 750px) {
            position: relative;
            bottom: 45px;
            justify-content: center;
        }
    }
    .rocYear-title {
        position: relative;
        top: 38px;
        text-align: center;
        font-size: 28px;
        font-weight: bold;
    }
    .for-calendar > .ant-btn {
        // background-color: #2c3e50;
        // border: #2c3e50 1px solid;
        background-color: $secondary-color;
        border: $secondary-color 1px solid;
        color: white;
        line-height: 0;
    }
    .for-calendar > .ant-btn[disabled] {
        background-color: #d9d9d9;
        border: #d9d9d9 1px solid;
        color: white;
        line-height: 0;
    }
    .for-calendar > .ant-btn:hover:not([disabled]),
    .for-calendar > .ant-btn:active:not([disabled]),
    .for-calendar > .focus-today {
        background-color: lighten($secondary-color, 10%);
        border: lighten($secondary-color, 10%) 1px solid;
    }
}
