@import "custom-setting.scss";

.merc-card {
    display: block;
    padding: map-get($base-card, padding);
    border: map-get($base-card, border);
    box-shadow: map-get($base-card, box-shadow);
    background-color: map-get($base-card, background-color);
    border-radius: map-get($base-card, border-radius);

    .merc-card-header {
        display: flex;
        align-items: center;
        align-self: map-get($base-card-header, align-self);
        font-size: map-get($base-card-header, font-size);
        font-weight: map-get($base-card-header, font-weight);
        padding: map-get($base-card-header, padding);
        border-radius: map-get($base-card, border-radius) map-get($base-card, border-radius) 0 0;
        color: $color-white;
    }

    .merc-card-content {
        display: block;
        padding: map-get($base-card-content, padding);
    }

    /* header 背景顏色 start */
    // 主題色
    &.merc-card-primary > .merc-card-header {
        background-color: $primary-color;
    }
    // 主題色變形
    &.merc-card-primary-variant > .merc-card-header {
        background-color: $primary-variant-color;
    }
    // 次要色
    &.merc-card-secondary > .merc-card-header {
        background-color: $secondary-color;
    }
    // 次要色變形
    &.merc-card-secondary-variant > .merc-card-header {
        background-color: $secondary-variant-color;
    }
    // 第三色
    &.merc-card-third > .merc-card-header {
        background-color: $third-color;
    }
    // 第三色變形
    &.merc-card-third-variant > .merc-card-header {
        background-color: $third-variant-color;
    }
    /* header 背景顏色 end */

    /* header 字體顏色 start */
    // 主題色
    @each $name, $color in $colorSet {
        .merc-card-header-#{$name} {
            color: $color;
        }
    }
    /* header 顏色 end */

    /* header 高度 start */
    // 小
    &.merc-card-small > .merc-card-header {
        height: 30px;
    }
    // 中
    &.merc-card-normal > .merc-card-header {
        height: 44px;
    }
    // 大
    &.merc-card-large > .merc-card-header {
        height: 50px;
    }
    /* header 高度 end */
}
