/* 此scss為元件內的 custom-setting，與圖書館全域的 custom-setting 不同 */
/** 基礎功能色設定 */
$color-white: #fff !default;
$color-dark: rgba(0, 0, 0, 0.8) !default;
// 主要色
$primary-color: #b9e1e4 !default;
// 主要色變形
$primary-variant-color: #54be9f !default;
// 次要色
$secondary-color: #4b8ac5 !default;
// 次要色變形
$secondary-variant-color: #e6f3f7 !default;
// 第三色
$third-color: #800080 !default;
// 第三色變形
$third-variant-color: #eed7ed !default;

// 淺灰
$color-light-gray: #e5e5e5 !default;

// 顏色定義包
$colorSet: "primary" $primary-color, "primary-variant" $primary-variant-color, "secondary" $secondary-color,
  "secondary-variant" $secondary-variant-color, "third" $third-color, "third-variant" $third-variant-color;

/** 基礎元件設定 */
$font-sz: (
  xl: 30px,
  lg: 18px,
  md: 16px,
  sm: 14px,
  xs: 12px,
) !default;

$font-weight: (
  fatty: 600,
  normal: normal,
) !default;

/** 常用元件設定 */
/* base btn start */
$btn: (
  border-radius: 7px,
  shadow: 1px 1px 2px 0 rgba(21, 28, 42, 0.17),
) !default;
$btn-height: (
  lg: 40px,
  md: 35px,
  sm: 30px,
) !default;
$btn-width: (
  xl: 200px,
  lg: 150px,
  md: 120px,
  sm: 90px,
  xs: 60px,
) !default;
/* base btn end */

/** base card start*/
$base-card: (
  background-color: $color-white,
  box-shadow: none,
  padding: 0,
  border: 0,
  border-radius: 6px,
) !default;

$base-card-header: (
  align-self: center,
  font-size: map-get($font-sz, lg),
  font-weight: map-get($font-weight, fatty),
  padding: 0 15px,
) !default;

$base-card-content: (
  padding: 10px 15px,
) !default;
/** base card end*/

/* base table start */
$base-table: (
  // hover 顏色
  hover-background-color: darken($color-light-gray, 15%),
  // 間隔色
  even-col-color: $color-light-gray,
  // 圓角
  radius: 7px,
  // 陰影
  shadow: 1px 2px 4px #d5d5d5,
  // 表頭、內容（th、td） 間隔
  padding: 4px 8px,
  // 手機版表頭、內容（th、td） 間隔（flex table 專用）
  mo-padding: 10px 8px,
  // 幾px以下使用手機版（flex table 專用）
  break-point: 991px,
  // 欄位下方的border-bottom（flex table 專用）
  mo-border-bottom-width: 1px
) !default;

/* base tab start */
$base-tab: (
  // 顏色(merc-tab 未傳入 mercType 時有效)
  tab-color: $primary-color,
  // tab 編框
  tab-border-width: 1px,
  // content 編框
  content-border-width: 10px,
  // 圓角
  radius: 7px,
  // 字體大小
  font-size: map-get($font-sz, lg)
) !default;
/* base tab end */
