@import "custom-setting.scss";

/* overwrite tab style */
merc-tab {
    display: block;
    position: relative;

    .ant-tabs.ant-tabs-card {
        .ant-tabs-nav {
            border: 0;
            margin-bottom: 0px;
            &::before {
                border-bottom: hidden;
            }
            .ant-tabs-tab {
                border: 0;
                font-size: map-get($base-tab, font-size);
                font-weight: 600;
                color: map-get($base-tab, tab-color);
                border-top-left-radius: map-get($base-tab, radius);
                border-top-right-radius: map-get($base-tab, radius);
                border-width: map-get($base-tab, tab-border-width);
                border-style: solid;
                border-color: map-get($base-tab, tab-color);
                border-bottom-width: 0px !important;
                padding: 0 15px;
                height: 35px;

                /* 關閉的叉叉 */
                .ant-tabs-close-x {
                    color: inherit;
                    font-size: 16px;
                    height: 18px;
                    margin-right: 0 !important;
                    vertical-align: middle;
                }
            }
            .ant-tabs-tab-active {
                background-color: map-get($base-tab, tab-color);
                border-width: map-get($base-tab, tab-border-width);
                border-color: map-get($base-tab, tab-color);
                border-style: solid;

                .ant-tabs-tab-btn {
                    font-weight: 600;
                    color: $color-white;
                }
            }
        }

        .ant-tabs-tabpane {
            background-color: $color-white;
            border-width: map-get($base-tab, content-border-width);
            border-color: map-get($base-tab, tab-color);
            border-style: solid;
            border-top-right-radius: map-get($base-tab, radius);
            border-bottom-right-radius: map-get($base-tab, radius);
            border-bottom-left-radius: map-get($base-tab, radius);
        }
    }

    /* 顏色 start */
    // 主題色
    &.merc-tab-primary {
        > .ant-tabs.ant-tabs-card {
            > .ant-tabs-nav {
                .ant-tabs-tab {
                    color: $primary-color;
                    border-color: $primary-color;
                }
                .ant-tabs-tab.ant-tabs-tab-active {
                    background-color: $primary-color;
                    border-color: $primary-color;
                }
            }

            > .ant-tabs-content-holder {
                > .ant-tabs-content {
                    > .ant-tabs-tabpane {
                        border-color: $primary-color;
                    }
                }
            }
        }
    }
    // 主題色變形
    &.merc-tab-primary-variant {
        > .ant-tabs.ant-tabs-card {
            > .ant-tabs-nav {
                .ant-tabs-tab {
                    color: $primary-variant-color;
                    border-color: $primary-variant-color;
                }
                .ant-tabs-tab.ant-tabs-tab-active {
                    background-color: $primary-variant-color;
                    border-color: $primary-variant-color;
                }
            }

            > .ant-tabs-content-holder {
                > .ant-tabs-content {
                    > .ant-tabs-tabpane {
                        border-color: $primary-variant-color;
                    }
                }
            }
        }
    }
    // 次要色
    &.merc-tab-secondary {
        > .ant-tabs.ant-tabs-card {
            > .ant-tabs-nav {
                .ant-tabs-tab {
                    color: $secondary-color;
                    border-color: $secondary-color;
                }
                .ant-tabs-tab.ant-tabs-tab-active {
                    background-color: $secondary-color;
                    border-color: $secondary-color;
                }
            }

            > .ant-tabs-content-holder {
                > .ant-tabs-content {
                    > .ant-tabs-tabpane {
                        border-color: $secondary-color;
                    }
                }
            }
        }
    }
    // 次要色變形
    &.merc-tab-secondary-variant {
        > .ant-tabs.ant-tabs-card {
            > .ant-tabs-nav {
                .ant-tabs-tab {
                    color: $secondary-variant-color;
                    border-color: $secondary-variant-color;
                }
                .ant-tabs-tab.ant-tabs-tab-active {
                    background-color: $secondary-variant-color;
                    border-color: $secondary-variant-color;
                }
            }

            > .ant-tabs-content-holder {
                > .ant-tabs-content {
                    > .ant-tabs-tabpane {
                        border-color: $secondary-variant-color;
                    }
                }
            }
        }
    }
    // 第三色
    &.merc-tab-third {
        > .ant-tabs.ant-tabs-card {
            > .ant-tabs-nav {
                .ant-tabs-tab {
                    color: $third-color;
                    border-color: $third-color;
                }
                .ant-tabs-tab.ant-tabs-tab-active {
                    background-color: $third-color;
                    border-color: $third-color;
                }
            }

            > .ant-tabs-content-holder {
                > .ant-tabs-content {
                    > .ant-tabs-tabpane {
                        border-color: $third-color;
                    }
                }
            }
        }
    }
    // 第三色變形
    &.merc-tab-third-variant {
        > .ant-tabs.ant-tabs-card {
            > .ant-tabs-nav {
                .ant-tabs-tab {
                    color: $third-variant-color;
                    border-color: $third-variant-color;
                }
                .ant-tabs-tab.ant-tabs-tab-active {
                    background-color: $third-variant-color;
                    border-color: $third-variant-color;
                }
            }

            > .ant-tabs-content-holder {
                > .ant-tabs-content {
                    > .ant-tabs-tabpane {
                        border-color: $third-variant-color;
                    }
                }
            }
        }
    }
    /* 顏色 end */

    /* tab 高度 start */
    // 小
    &.merc-tab-small {
        > .ant-tabs-card > .ant-tabs-nav {
            .ant-tabs-tab {
                height: 25px;
            }
        }
    }
    // 中
    &.merc-tab-normal {
        > .ant-tabs-card > .ant-tabs-nav {
            .ant-tabs-tab {
                height: 35px;
            }
        }
    }
    // 大
    &.merc-tab-large {
        > .ant-tabs-card > .ant-tabs-nav {
            .ant-tabs-tab {
                height: 40px;
            }
        }
    }
    /* tab 高度 end */
}
/* end of overwrite tab style */
